<template>
    <div>
      <el-breadcrumb separator-class="el-icon-arrow-right" class="el-breadcrumb">
        <el-breadcrumb-item :to="{path:'/main'}">首页</el-breadcrumb-item>
        <el-breadcrumb-item>物业信息管理</el-breadcrumb-item>
        <el-breadcrumb-item>设备管理</el-breadcrumb-item>
      </el-breadcrumb>

      <el-card class="el-card">

        <el-row :gutter="10">
          <el-col :span="8">
            <el-input placeholder="请输入设备名称" v-model="deviceName">
              <el-button slot="append" icon="el-icon-search" @click="handleSearch"></el-button>
            </el-input>
          </el-col>
          <el-col :span="4">
            <el-button type="primary" @click="addDialogVisible=true">添加车位</el-button>
          </el-col>
        </el-row>

        <el-table :data="tableData">
          <el-table-column prop="id" label="序号" width="70"></el-table-column>
          <el-table-column prop="deviceNumber" label="设备编号" width="120"></el-table-column>
          <el-table-column prop="deviceName" label="设备名称" width="140"></el-table-column>
          <el-table-column prop="devicePlace" label="设备地址" width="140"></el-table-column>
          <el-table-column prop="deviceTime" label="购买时间" width="120"></el-table-column>
          <el-table-column prop="deviceCost" label="设备花费" width="120"></el-table-column>
          <el-table-column label="操作" min-width="250">
            <template slot-scope="scope">
              <el-button type="primary" @click="showReadDialog(scope.row.id)" icon="el-icon-reading">查看</el-button>
              <el-button type="warning" @click="showEditDialog(scope.row.id)" icon="el-icon-edit">修改</el-button>
              <el-button type="danger" icon="el-icon-delete" @click="removeDeviceById(scope.row.id)">删除</el-button>
            </template>
          </el-table-column>
        </el-table>

        <el-pagination
          background
          @current-change="handleCurrentChange"
          :current-page="currentPage"
          :page-size="pageSize"
          layout="total,prev, pager, next"
          :total="total">
        </el-pagination>
      </el-card>

      <el-dialog
        title="添加设备"
        :visible.sync="addDialogVisible"
        width="40%"
      >
        <el-form :model="addForm" :rules="addFormRules" ref="addFormRef" label-width="90px" @close="addDialogClosed">
          <el-form-item label="设备编号" prop="deviceNumber">
            <el-input v-model="addForm.deviceNumber"></el-input>
          </el-form-item>
          <el-form-item label="设备名称" prop="deviceName">
            <el-input v-model="addForm.deviceName"></el-input>
          </el-form-item>
          <el-form-item label="设备地址" prop="devicePlace">
            <el-input v-model="addForm.devicePlace"></el-input>
          </el-form-item>
          <el-form-item label="购买时间" prop="deviceTime">
            <el-input v-model="addForm.deviceTime"></el-input>
          </el-form-item>
          <el-form-item label="设备花费" prop="deviceCost">
            <el-input v-model="addForm.deviceCost"></el-input>
          </el-form-item>
        </el-form>

        <span slot="footer" class="dialog-footer">
        <el-button @click="addDialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="addDevice">确 定</el-button>
      </span>
      </el-dialog>

      <el-dialog
        title="修改设备信息"
        :visible.sync="editDialogVisible"
        width="40%"
      >
        <el-form :model="editForm" :rules="editFormRules" ref="editFormRef" label-width="90px" @close="editDialogClosed">
          <el-form-item label="设备编号" prop="deviceNumber">
            <el-input v-model="editForm.deviceNumber"></el-input>
          </el-form-item>
          <el-form-item label="设备名称" prop="deviceName">
            <el-input v-model="editForm.deviceName"></el-input>
          </el-form-item>
          <el-form-item label="设备地址" prop="devicePlace">
            <el-input v-model="editForm.devicePlace"></el-input>
          </el-form-item>
          <el-form-item label="购买时间" prop="deviceTime">
            <el-input v-model="editForm.deviceTime"></el-input>
          </el-form-item>
          <el-form-item label="设备花费" prop="deviceCost">
            <el-input v-model="editForm.deviceCost"></el-input>
          </el-form-item>
        </el-form>

        <span slot="footer" class="dialog-footer">
          <el-button @click="editDialogVisible = false">取 消</el-button>
          <el-button type="primary" @click="editDevice">确 定</el-button>
        </span>
      </el-dialog>

      <el-dialog
        title="查看"
        :visible.sync="readDialogVisible"
        width="40%"
      >
        <el-form :model="readForm" ref="readFormRef" label-width="90px" >
          <el-form-item label="设备编号" prop="deviceNumber">
            <el-input v-model="readForm.deviceNumber" :disabled="true"></el-input>
          </el-form-item>
          <el-form-item label="设备名称" prop="deviceName">
            <el-input v-model="readForm.deviceName" :disabled="true"></el-input>
          </el-form-item>
          <el-form-item label="设备地址" prop="devicePlace">
            <el-input v-model="readForm.devicePlace" :disabled="true"></el-input>
          </el-form-item>
          <el-form-item label="购买时间" prop="deviceTime">
            <el-input v-model="readForm.deviceTime" :disabled="true"></el-input>
          </el-form-item>
          <el-form-item label="设备花费" prop="deviceCost">
            <el-input v-model="readForm.deviceCost" :disabled="true"></el-input>
          </el-form-item>
        </el-form>

        <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="readDialogVisible = false">关  闭</el-button>
      </span>
      </el-dialog>

    </div>
</template>

<script>
    export default {
        name: "deviceManage",
      data() {
        return {
          pageSize: 8,
          currentPage: 1,
          tableData: {
            id:"",
            deviceNumber:"",
            deviceName:"",
            devicePlace:"",
            deviceTime:"",
            deviceCost:""
          },
          total: 0,
          deviceName: "",
          addDialogVisible: false,
          addForm:{
            id:"",
            deviceNumber:"",
            deviceName:"",
            devicePlace:"",
            deviceTime:"",
            deviceCost:""
          },
          addFormRules:{
            deviceNumber:[
              {required: true, message: '设备编号不可为空', trigger: 'blur'},
              {min:1,max:20,message: '设备编号长度在2~20个字符之间',trigger:'blur'}
            ],
            deviceName:[
              {required: true, message: '设备名称不可为空', trigger: 'blur'}
            ],
            devicePlace:[
              {required: true, message: '设备地址不可为空', trigger: 'blur'}
            ],
            deviceTime:[
              {required: true, message: '购买时间不可为空', trigger: 'blur'}
            ],
            deviceCost:[
              {required: true, message: '设备花费不可为空', trigger: 'blur'}
            ]
          },
          editDialogVisible:false,
          editForm:{
            id:"",
            deviceNumber:"",
            deviceName:"",
            devicePlace:"",
            deviceTime:"",
            deviceCost:""
          },
          editFormRules:{
            deviceNumber:[
              {required: true, message: '设备编号不可为空', trigger: 'blur'},
              {min:1,max:20,message: '设备编号长度在2~20个字符之间',trigger:'blur'}
            ],
            deviceName:[
              {required: true, message: '设备名称不可为空', trigger: 'blur'}
            ],
            devicePlace:[
              {required: true, message: '设备地址不可为空', trigger: 'blur'}
            ],
            deviceTime:[
              {required: true, message: '购买时间不可为空', trigger: 'blur'}
            ],
            deviceCost:[
              {required: true, message: '设备花费不可为空', trigger: 'blur'}
            ]
          },
          readDialogVisible:false,
          readForm:{
            id:"",
            deviceNumber:"",
            deviceName:"",
            devicePlace:"",
            deviceTime:"",
            deviceCost:""
          }
        }
      },
      created() {
        this.queryDeviceList()
      },
      methods: {
        queryDeviceList() {
          this.$http.get('device/queryDevice',
            {params: {currentPage: this.currentPage, pageSize: this.pageSize, deviceName: this.deviceName}})
            .then(res => {
              this.total = res.data.total;
              this.tableData = res.data.data;
            });
        },
        handleCurrentChange(currentPage) {
          this.currentPage = currentPage
          this.queryDeviceList()
        },
        handleSearch() {
          this.currentPage = 1
          this.queryDeviceList()
        },
        addDialogClosed(){
          this.$refs.addFormRef.resetFields()
        },
        addDevice(){
          this.$refs.addFormRef.validate(async valid=>{
            if(!valid) return
            const {data:res}=await this.$http.post("device/addDevice",this.addForm)
            if(res.code!==200){
              this.$message.error("添加设备信息失败,请检查设备信息")
            }else {
              this.$message.success("添加设备信息成功")
              this.addDialogVisible=false
              this.queryDeviceList()
            }
          })
        },
        showEditDialog(id){
          this.editDialogVisible =true
          this.$http.get('device/getDeviceById',{params:{id:id}})
            .then(res => {
              this.editForm = res.data;
            });
        },
        editDialogClosed(){
          this.$refs.editFormRef.resetFields()
        },
        editDevice(){
          this.$refs.editFormRef.validate(async valid =>{
            if(!valid) return
            const {data:res}=await this.$http.post("device/editDevice",this.editForm)
            if(res.code!==200){
              this.$message.error("修改设备信息失败,请检查设备信息")
            }else {
              this.$message.success("修改设备信息成功")
              this.editDialogVisible=false
              this.queryDeviceList()
            }
          })
        },
        removeDeviceById(id){
          this.$confirm('此操作将永久删除, 是否继续?', '提示', {
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            type: 'warning'
          }).then(() => {
            this.$http.get('device/removeDeviceById',{params:{id:id}});
            this.$message({
              type: 'success',
              message: '删除成功!'
            });
            this.queryDeviceList();
          }).catch(() => {
            this.$message({
              type: 'info',
              message: '已取消删除'
            });
          });
        },
        showReadDialog(id){
          this.readDialogVisible =true
          this.$http.get('device/getDeviceById',{params:{id:id}})
            .then(res => {
              this.readForm = res.data;
            });
        },
      }
    }
</script>

<style scoped lang="scss">
  .el-breadcrumb {
    margin-bottom: 15px;
    font-size: 17px;
  }

  .el-card {
    box-shadow: 0 1px 1px rgba(0, 0, 0, 0.15);
  }
</style>



